.navbody {
     display: flex;
   top: 0;
   position: fixed;
   width: 100vw;
   min-height: 50px;
   max-height: 130px;
   border-bottom: 1px solid #e5e5e5;
   box-sizing: border-box;
   backdrop-filter: saturate(180%) blur(5px);
z-index: 1100;
   justify-content: space-between;
  font-family: 'Roboto', sans-serif;
position: relative;
 }
 
 .flexx{
    
 padding: 0px 20px;
 }
 
 .hs{
     padding: 18px 50px;
     font-size: 28px;
     color: #286DAB;
     cursor: pointer;
      transition: 0.5s;
      
 }
 .hs:hover{
     transform: scale(1.3);
    
 }
 
 .flexx > a{
 padding: 0px 20px;
 font-weight: 550;
 cursor: pointer;
 display: inline-block;
 margin-top: 2.5vh;
 }
 
 .flexx a::before{
     content: "";
   position: absolute;
     width: 0vw;
     margin-top: 20px;
     height: 3px;
     transition: 0.3s; 
     background-color: #42A5F5;
 }
 
 .flexx .a1:hover::before{
      width: 2.8em;
 }
 
 .flexx .a2:hover::before{
      width: 2.9em;
 }
 
 .flexx .a3:hover::before{
      width: 3.3em;
 }
 
 .flexx .a4:hover::before{
      width: 2.6em;
 }
 
 .flexx .a5:hover::before{
      width: 3.6em;
 }
 
 .flexx a{
      text-decoration :none;
      color: black;
 }
 
 .mobilemenuicon{
      display: none;
 }
  
 
 
 @media only screen and (max-width: 780px){
  .navbody{
      display: flex;
      position: relative;
    height: 90%;
  }    
 .flexx{
    display: none;
  
     
 } 
 
  .navlinksmobile{
     background-color:#e5e5e5;
      position: absolute;
 left:0;
 top:75px;
 height: auto;
 width:100%;
 
      
 } 
 .a1,.a2,.a3,.a4,.a5,.a6{
      display: block;
      text-align: center;
      padding: 12px;
      transition: all 0.5s ease-out;
 width:100%;
 }
 .mobilemenuicon
 {
      
   display: block;
   position: absolute;
   font-size: 30px;
   background-color: black;
   color: #e5e5e5;
   border: none;
   outline: none;
   top: 15px;
   right: 25px;
 }  
 
 }
 
 .resume{
      border: none;
      font-weight: 550;
    padding: 5px;
    color: black;
    cursor: pointer;
    border-radius: 50%;
    min-width: 120px;
    transition: 0.4s ease;
    font-size: larger;
    }
